function bindleftCommonSearchdropdown(instance, fieldName) {
    debugger
    var vm = instance;
    if (fieldName == 'Client') {
        var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&reqFrom='CUSTOM'&search=${vm.searchCondition}&userId=${vm.GetUserInfo.ID}`;

        DataService.GetParentClientGroupNew(url).then((response) => {
            debugger;

            const ClientNameData = convertApiTreeToFlatList(response.data.result);
            if (ClientNameData.length > 0) {
                vm.leftSearchFields[0].listOptions = [];
                ClientNameData.forEach(element => {
                    let Obj = {
                        value: `${element.CHILD_USER_ID}`,
                        name: element.CHILD_NAME
                    }
                    vm.leftSearchFields[0].listOptions.push(Obj);
                });
            }
            // if (response.data.result != "" && response.data.result != null) {
            //     if (response.data.result.length > 0) {
            //         vm.leftSearchFields[0].listOptions = [];
            //         response.data.result.forEach(element => {
            //             let Obj = {
            //                 value: element.chilD_USER_ID,
            //                 name: element.chilD_NAME
            //             }
            //             vm.leftSearchFields[0].listOptions.push(Obj);
            //         });
            //     }
            // }
        });
    }
    if (fieldName == 'WORKCODE') {
        DataService.GetWorkCodes().then((response) => {
            if (response.data.result.length > 0) {
                vm.leftSearchFields[1].listOptions = [];
                response.data.result.forEach(element => {
                    let Obj = {
                        value: `${element.Value}`,
                        name: element.Text
                    }
                    vm.leftSearchFields[1].listOptions.push(Obj);
                });
            }
        });
    }    
}
function convertApiTreeToFlatList(tree) {
    let flat = [];

    function walkParent(node) {
        // Push parent
        flat.push({
            CHILD_USER_ID: node.parenT_ID,
            CHILD_NAME: node.parenT_NAME,
            PARENT_ID: null
        });

        // Push children
        if (node.children && node.children.length > 0) {
            node.children.forEach(child => {
                flat.push({
                    CHILD_USER_ID: child.chilD_USER_ID,
                    CHILD_NAME: child.chilD_NAME,
                    PARENT_ID: node.parenT_ID
                });
            });
        }
    }
    tree.forEach(root => walkParent(root));
    return flat;
}

export default { buttons, leftsearchSchema, listActions, bindleftCommonSearchdropdown }; 